import React, {Component} from 'react'
import { css } from '@emotion/core'
import { Table, Card, Button, Tooltip, Modal } from 'antd'
import { Link } from 'Config/routes'

class ListOfEmployee extends Component {

  state = {
    loading: false,
    visible: false,
  }

  showModal = () => {
    this.setState({
      visible: true,
    })
  }

  handleOk = () => {
    this.setState({ loading: true })
    setTimeout(() => {
      this.setState({ loading: false, visible: false })
    }, 3000)
  }

  handleCancel = () => {
    this.setState({ visible: false })
  }
  
  render(){
    const { visible } = this.state

    const columns = [{
      title: 'Nama',
      dataIndex: 'nama',
    }, {
      title: 'Nik',
      dataIndex: 'nik',
      defaultSortOrder: 'descend',
      sorter: (a, b) => a.nik - b.nik,
    }, {
      title: 'Jabatan',
      dataIndex: 'jabatan'
    }, {
      title: 'Shift',
      dataIndex: 'shift'
    }, {
      title: 'Aksi', dataIndex: '', key: 'x', render: () => (
        <div>
          <Tooltip placement="bottom" title="Detail">
            <Button icon="more" onClick={this.showModal} />
          </Tooltip>
        </div>
      )
    }]
    
    const data = [{
      key: '1',
      nama: 'John Brown',
      nik: 11112345,
      jabatan: 'Staf Kasir',
      shift: 'Shift 1'
    }, {
      key: '2',
      nama: 'Jim Green',
      nik: 11112346,
      jabatan: 'Staf Kasir',
      shift: 'Shift 2'
    }, {
      key: '3',
      nama: 'Koko Fork',
      nik: 11112346,
      jabatan: 'Staf Kasir',
      shift: 'Shift 3'
    }, {
      key: '4',
      nama: 'Lala Green',
      nik: 11112346,
      jabatan: 'Staf Kasir',
      shift: 'Shift 4'
    }]

    return(
      <>
      <div><h2>Daftar Karyawan</h2></div>
      <Card css={css`
        border-radius: 8px;
        border-top: 4px solid rgb(24, 144, 255);
        box-shadow: 1px 4px 15px rgba(24, 144, 255, 0.1);
        @media(max-width: 700px) {
          width: 290px;
        }
      `}>
        <div css={css`display: flex; justify-content: flex-end; margin-bottom: 1em;`}>
          <Link route="addEmployee">
            <Button type="primary">
              Tambah Karyawan
            </Button>
          </Link>
        </div>
        <div css={css`width: 250px; overflow-x: auto;`}>
          <Table columns={columns} dataSource={data} />
        </div>
      </Card>
      <Modal
        visible={visible}
        title="Detail"
        onOk={this.handleOk}
        onCancel={this.handleCancel}
        okButtonProps={{ disabled: true }}
        cancelButtonProps={{ disabled: true }}
        width="700px"
      >
        <div css={css`
          display: grid;
          grid-template-columns: 1fr 2fr;
          grid-column-gap: 1em;
          justify-items: center;
        `}>
          <div css={css`
            height: 150px;
            width: 150px;
            border-radius: 50%;
            position: relative;
            background-size: cover;
            background-repeat: no-repeat;
            background-image: url('/static/defaultplaceholder.png');
          `} 
          className="photoEmployee"
          />
          <div css={css`
              display: grid;
              grid-row-gap: 2em;
            .detailContent{
              display: grid;
              grid-template-columns: 1fr 2fr;
              grid-column-gap: 1em;
            }
          `}>
            <div className="detailContent">
              <label>Nama Lengkap</label>
              <label><span>:</span> Alifia Indra</label>
            </div>
            <div className="detailContent">
              <label>NIK</label>
              <label><span>:</span> 1223344</label>
            </div>
            <div className="detailContent">
              <label>Jenis Kelamin</label>
              <label><span>:</span> Pria</label>
            </div>
            <div className="detailContent">
              <label>Tempat/Tanggal Lahir</label>
              <label><span>:</span> Jakarta, 22 Maret 1993</label>
            </div>
            <div className="detailContent">
              <label>No. Telepon</label>
              <label><span>:</span> 087676556458</label>
            </div>
            <div className="detailContent">
              <label>Jabatan</label>
              <label><span>:</span> Staf Kasir</label>
            </div>
            <div className="detailContent">
              <label>Shift</label>
              <label><span>:</span> Shift I | 06.00 - 12.00</label>
            </div>
          </div>
        </div>
      </Modal>
      </>
    )
  }
}

export default ListOfEmployee